<template>
	<view>
		<u-sticky>
			<u-tabs :list="list" :current="0" @change="changeTab"></u-tabs>
		</u-sticky>
		<!-- search -->
		<view class="search">
			<u-search
				placeholder="搜索歌曲"
				v-model="keywords"
				:showAction="false"
				@search="search"
				@clear="clear">
			</u-search>
		</view>
		<!-- hotSearch -->
		<view v-if="keywords === ''">
			<u-divider text="热搜榜" class="search"></u-divider>
			<hot-search :indexList="hotSearch" @item-click='hotWordSearch'></hot-search>
		</view>
		<view v-else>
			<u-cell-group
				v-for="item in searchList"
				:key="item.id"
				>
				<u-cell
					:border="false"
					:title="item.name"
					:label="item.ar[0].name"
					:titleStyle="font_ellipsis"
					>
					<u-icon
						name="play-circle"
						size="20"
						slot="right-icon"
						@click="addPlayList(item)"></u-icon>
				</u-cell>
			</u-cell-group>
			<view v-if="!base && searchList.length">
				<u-loadmore
					:line="true"
					status="loadmore"
					@loadmore="loadmore"
				></u-loadmore>
			</view>
			<view v-else>
				<u-loadmore
					:line="true"
					status="nomore"
				></u-loadmore>
			</view>
		</view>
		<!-- 加载页 -->
		<u-loading-page
			:loadingText="loadingPageData.loadingText"
			:loadingMode="loadingPageData.loadingMode"
			:bgColor="loadingPageData.bgColor"
			:color="loadingPageData.color"
			:loading="loading"
			:loadingColor="loadingPageData.loadingColor"
			style="z-index: 999;"></u-loading-page>
		</u-button>
	</view>
</template>

<script>
	
	import { cloudSearch, getSong, getLyric, checkSongUrl, hotSearch } from '@/http/http.api.js';
	export default {
		data() {
			return {
				curt: 0,
				// list: ['搜索', '歌词'],
				list: [{
						name: '控制&评论',
					}, {
						name: '播放列表',
					}],
				
				keywords: '',
				limit: 30,
				offset: 1,
				
				searchList: [],
				
				// 加载页
				loading: false,
				loadingPageData: {
					loadingText: '正在搜索',
					loadingMode: 'semicircle',
					bgColor: 'rgba(0, 0, 0, 0.3)',
					color: '#ffffff',
					loadingColor: '#ffffff'
				},
				
				font_ellipsis: {
					'width': '80vw',
					'overflow': 'hidden',
					'white-space': 'nowrap',
					'text-overflow': 'ellipsis'
				},
				
				// 搜索到底
				base: false,
				
				// 热搜列表20条
				hotSearch: []
			}
		},
		methods: {
			changeTab(i) {
				this.curt = i.index;
				if(i.index === 0){
					this.$u.route('pages/audio/audio');
				}else{
					// this.$u.route('pages/comment/comment', {
					// 	songId: 
					// });
					this.$u.route('pages/play-list/play-list')
				}
			},
			// 歌曲搜索
			search(){
				this.loading = true;
				this.offset = 1;
				this.searchList.splice(0, this.searchList.length);
				cloudSearch({ params: { keywords: this.keywords, limit: this.limit, offset: (this.offset - 1) * this.limit } }).then(res => {
					this.loading = false;
					if(res.data.result.songs.length !== 0){
						res.data.result.songs.forEach((item, index) => {
							this.searchList.push(item);
						});
					}
					// console.log(res.data.result.songs);
				}).catch(e => {
					console.log(e);
				});
			},
			clear(){
				this.offset = 1;
				this.searchList.splice(0, this.searchList.length);
			},
			// 添加到播放列表
			addPlayList(song){
				this.loading = true;
				checkSongUrl({ params: { id: song.id } }).then(res => {
					getSong({ params: { id: song.id } }).then(res => {
							// 设置对象存键值
							let obj = {};
							this.$zaudio.audiolist.push({ id: song.id, src: res.data.data[0].url, title: song.name, singer: song.ar[0].name, coverImgUrl: song.al.picUrl });
							this.$zaudio.audiolist = this.$zaudio.audiolist.reduce((item, next) => {
								// 判断下一个对象中的src路径是否在obj对象中是真值，是真值存在则为空并不给item数组 push对象
								// 为假不存在则设置该对象的src为obj的键值并设为真值，追加该对象到item中
							    obj[next.src] ? '' : obj[next.src] = true && item.push(next);
							    return item;
							}, []);
							this.loading = false;
							this.$u.toast('添加成功');
					});
				}).catch(e => {
					console.log(e);
					this.loading = false;
					this.$u.toast(e.data.message);
					return;
				});
			},
			loadmore(){
				this.loading = true;
				this.offset = this.offset + 1;
				cloudSearch({ params: { keywords: this.keywords, limit: this.limit, offset: (this.offset - 1) * this.limit } }).then(res => {
					this.loading = false;
					if(!res.data.result.songCount){
						this.base = true;
						this.loading = false;
						this.$u.toast('到底了');
						return
					}
					if(res.data.result.songs){
						this.loading = false;
						res.data.result.songs.forEach((item, index) => {
							this.searchList.push(item);
						});
					}
					console.log(res.data.result.songs);
				}).catch(e => {
					console.log(e);
				});
			},
			hotWordSearch(keyword){
				console.log(keyword);
				this.keywords = keyword;
				this.search();
			}
		},
		onLoad() {
			hotSearch().then(res => {
				this.hotSearch = res.data.data;
			}).catch(e => {
				console.log(e);
			});
		},
		onUnload() {
			this.$off('item-click');
		}
	}
</script>

<style lang="scss" scoped>
	
	.search{
		padding: 20rpx 40rpx 20rpx;
	}
	
	// 文字超出省略
	.font_ellipsis{
		overflow:hidden;
		white-space:nowrap; 
		text-overflow:ellipsis;
	}
	
</style>
